import { Component, OnInit, Input } from '@angular/core';
import { AlertService } from 'src/app/api/controllers/Alert';
import { CookieService } from 'ngx-cookie-service';
import { ViolationService } from 'src/app/api/controllers/Violation';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';

@Component({
  selector: 'app-dash-com-list',
  templateUrl: './dash-com-list.component.html',
  styleUrls: ['./dash-com-list.component.scss']
})
export class DashComListComponent implements OnInit {
  public config: PerfectScrollbarConfigInterface = {};
  @Input() label:string;
  emergencyAlert = [];
  violation = [];
  workPermit = [];
  facility = [];

  constructor( private alertService: AlertService,
    private violationService: ViolationService,
    private cookieService:CookieService,
    private workpermitService: WorkPermitService,
    private facilityService: FacilityService,) { }

  ngOnInit(): void {
    let entity = {
      apartmentId:  parseInt(this.cookieService.get('apartmentId')),
      active: 1,
    }
    if(this.label == 'Emergency Alerts') {
      this.alertService.getAllApartmentBlockUnitAlertByApartmentIdStatus(entity).subscribe((res:any)=>{
        this.emergencyAlert = res
      })
    } else if(this.label == 'Recent Violations') {
      this.violationService.getAllViolationsByStatus(entity).subscribe((res:any)=>{
        this.violation = res
      })
    } else if(this.label == 'Work Permit') {
      this.workpermitService.getWorkPermitsByApartmentIdStatus(entity).subscribe((res:any) => {
        this.workPermit = res
      })
    } else if(this.label == 'Facility Bookings') {
      this.facilityService.getApartmentFacilityBookingsByApartmentIdStatus(entity).subscribe((res:any) => {
        this.facility = res
      })
    }
  }

}
