import { Component, OnInit, Input } from '@angular/core';
import { DateModelComponent } from '../date-model/date-model.component';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-admin-dash-first-row',
  templateUrl: './admin-dash-first-row.component.html',
  styleUrls: ['./admin-dash-first-row.component.scss']
})
export class AdminDashFirstRowComponent implements OnInit {
  @Input() label:any; 
  colType:string = 'owner';

  constructor(
    public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService) { }

  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  calenderPopUp(dateType,type,mode) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        if(type == 'Pending Admins' && mode == 'front') {
          this.dashboardService.getDashUnapprovedUsersPendingAdminbyApartmentId(result).subscribe((res:any)=>{
            this.label.frontValue = res.unapprovedUsersPendingAdmin;
          })
        } else if(type == 'Pending Users' && mode == 'back') {
          this.dashboardService.getDashUnapprovedUsersPendingusersbyApartmentId(result).subscribe((res:any)=>{
            this.label.backValue = res.unapprovedPendingusers;
          })
        } else if(type == 'Un Approve Move In' && mode == 'front') {
          this.dashboardService.getDashunapprovedmoveinbyApartmentId(result).subscribe((res:any)=>{
            this.label.frontValue = res.unapprovedMoveIn;
          })
        } else if (type == 'Un Approve Move Out' && mode == 'back') {
          this.dashboardService.getDashunapprovedmoveoutbyApartmentId(result).subscribe((res:any)=>{
            this.label.backValue = res.unapprovedMoveOut;
          })
        } else if (type == 'Up Coming Move In' && mode == 'front') {
          this.dashboardService.getDashupcomingmoveinApartmentId(result).subscribe((res:any)=>{
            this.label.frontValue = res.upcomingWeeklyApprovedMoveInCounts;     
          })
        }  else if (type == 'Up Coming Move Out' && mode == 'back') {
          this.dashboardService.getDashupcomingmoveoutApprovedbyApartmentId(result).subscribe((res:any)=>{
            this.label.backValue = res.upcomingMoveOut;
          })
        }
      }
    });
  }

  ngOnInit(): void {
  }
}
