import { Component, OnInit, Input } from '@angular/core';
import { DateModelComponent } from '../date-model/date-model.component';
import { MatDialog } from '@angular/material/dialog';
import { DashBoardService } from 'src/app/api/controllers/DashBoard';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-admin-dash-second-row',
  templateUrl: './admin-dash-second-row.component.html',
  styleUrls: ['./admin-dash-second-row.component.scss']
})
export class AdminDashSecondRowComponent implements OnInit {
  @Input() label:any; 
  constructor( public dialog: MatDialog,
    private dashboardService:DashBoardService,
    private cookieService:CookieService) { }

  flip(data) {
    let front = data.firstElementChild;
    let back = data.lastElementChild;
    if(front.classList.contains('flip-bk') && back.classList.contains('flip-ft')) {
      front.classList.remove('flip-bk');
      back.classList.remove('flip-ft');
    } else {
      front.classList.add('flip-bk');
      back.classList.add('flip-ft');
    }
  }

  calenderPopUp(dateType,type,mode) {
    const dialogRef = this.dialog.open(DateModelComponent, {
      width: '400px',
      height:'175px',
      data: dateType
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        if(type == 'Open Community' && mode == 'front') {
          this.dashboardService.getDashOpencommticketsbyApartmentId(result).subscribe((res:any)=>{
            this.label.frontValue = res.openCommunitytickets;
          })
        } else if(type == 'Open Personal' && mode == 'back') {
          this.dashboardService.getDashOpenpersonalticketsbyApartmentId(result).subscribe((res:any)=>{
            this.label.backValue = res.openPersonalticket;
          })
        } else if(type == 'High Priority Community' && mode == 'front') {
          this.dashboardService.getDashHighpriorityopenCommunityTicketsbyApartmentId(result).subscribe((res:any)=>{
            this.label.frontValue = res.highPriorityOpenCommunityTicket;
          })
        } else if (type == 'High Priority Personal' && mode == 'back') {
          this.dashboardService.getDashHighpriorityopenpersonalTicketsbyApartmentId(result).subscribe((res:any)=>{
            this.label.backValue = res.highPriorityOpenPersonalTicket;
          })
        } else if (type == 'Unassigned Community' && mode == 'front') {
          let params = {
            fromDate : result.date,
            apartmentId:result.apartmentId
          }
          this.dashboardService.getDashUnassignedCommunityTicketsbyApartmentId(params).subscribe((res:any)=>{
            this.label.frontValue = res.unassignedCommunityTickets;     
          })
        } else if (type == 'Unassigned Personal' && mode == 'back') {
          let params = {
            fromDate : result.date,
            apartmentId:result.apartmentId
          }
          this.dashboardService.getDashUnassignedpersonalTicketsbyApartmentId(params).subscribe((res:any)=>{
            this.label.backValue = res.unassignedPersonalTickets;
          })
        } else if (type == 'Open Community Aged' && mode == 'front') {
          this.dashboardService.getDashOpenCommunityAgedTicketsbyApartmentIDbyDays(result).subscribe((res:any)=>{
            this.label.frontValue = res.openCommunityAgedTickets;     
          })
        } else if (type == 'Open Personal Aged' && mode == 'back') {
          this.dashboardService.getDashOpenPersonalAgedTicketsbyApartmentIDbyDays(result).subscribe((res:any)=>{
            this.label.backValue = res.openPersonalAgedTickets;
          })
        } else if (type == 'InProgress Panic Alerts' && mode == 'front') {
          let params = {
            fromDate : result.date,
            apartmentId:result.apartmentId
          }
          this.dashboardService.getDashinProgressPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
            this.label.frontValue = res.inProgressPanicAlerts;
          });
        } else if (type == 'Open Panic Alerts' && mode == 'back') {
          let params = {
            fromDate : result.date,
            apartmentId:result.apartmentId
          }
          this.dashboardService.getDashOpenPanicAlertsbyApartmentId(params).subscribe((res:any)=>{
            this.label.backValue = res.openPanicAlerts;
          });
        } else if (type == 'Visitors IN' && mode == 'front') {
          let params = {
            startDate : result.fromDate,
            EndDate : result.toDate,
            apartmentId : result.apartmentId
          }
          this.dashboardService. getDashVisitorsCheckedinbyApartmentId(params).subscribe((res:any)=>{
            this.label.frontValue = res.visitorsCheckIn;
          });
        }
      }
    });
  }

  ngOnInit(): void {
  }
}
